*{
    font-family: sans-serif;
}
body{
    background-color: rgb(203, 231, 255);
    margin: 0;
    padding: 0;
}
a{
    color: black;
    text-decoration: none;
}


.NavBar_section{
    padding: 1rem;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    background-color: white;
    font-size: 0.75rem;
}

.NavBar_display img{
    width: 100px;
}
.NavBar_section input{
    width: 40%;
    font-size: 2rem;
}


.NavBar_icon_link{
    color: red;
    margin: 0.5rem 0.35rem;
    padding: 0.5rem 0.75rem;
    border: 1px solid red;
}
.NavBar_icon_link:hover{
    color: black;
    border: 1px solid black;
}
.NavBar_icon_link i{
  margin: 0.5rem 0.25rem;
}
.NavBar_icon_link p{
    display: inline;
}
.product_section{
    display: flex;
    justify-content: space-around;
    flex-wrap: wrap;
}
.comment_section{
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-around;
}

.product_box{
  margin: 1rem;
  display: flex;
  flex-direction: column;
  width: 350px;
  text-align: center;
  background-color: rgb(0, 0, 0);
  color: aliceblue;
  transition: 0.3s all;
}
.product_box:hover{
  background-color: rgb(255, 157, 157);
  color: white;
}
.product_box:hover ~ .product_box_info_purchase{
    background-color: white;
}
.product_box_display {
    margin: 0;
    padding: 0;
    height: 450px;
}
.product_box_display img{
    width: 100%;
    height: 100%;
    
}

.product_box_display_hover{
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
}
.product_box_display_hover div{
    align-self: center;
    display: inline;
    padding: 0;
    margin-right: 1rem;
}
.product_box_display_hover div:last-child{
    margin-right: 0;
}
.product_box_info{
    margin: 0 0.5rem;
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.product_box_info_purchase{
    display: flex;
    align-items: center;
    height: 35px;
    border-radius: 0.25rem;
    font-size: 0.85rem;

}
.product_box_info_purchase i{
    margin: 0;
    margin-left: 0.25rem;
}
.product_box_info_purchase p{
    margin: 0 0.3rem;
}


.purchase_cart_title_product_box_icon{
    height: 20px;
    padding: 0.4rem;
    background-color: red;
    color: white;
    transition: all 0.2s ease;
    text-align: center;
}


.purchase_cart_title_product_box_icon:hover{
    background-color: rgb(120, 255, 120);
    color: black;
}
.comment_container{
    display: flex;
    flex-direction: row;
    margin: 2rem;
    padding-right: 2rem;
    background-color: white;
}
.comment_container_display{
      width: 200px;
    height: 200px;
}
.comment_container_display img{
    width: 100%;
    height: 100%;
}
.comment_container_title{
    margin-left: 2rem;
}



.purchase_cart_section{
    padding-top: 5rem;
    padding-bottom: 5rem;
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    background-color: white;
}
.purchase_cart_title {
}
    
.purchase_cart_title_product{
    margin: 0;
    padding: 0;
    list-style-type: none;
    display: flex;                  
}
.purchase_cart_title_product li{
    padding: 2rem;
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    border: 0 solid black;
    border-width: 0 1px 1px 1px;
}
.purchase_cart_title_product:first-child li {
    border-width: 1px 1px 1px 0;
}

.purchase_cart_title_product li:first-child{
    width: 300px;
    justify-content: flex-start;
    border-width: 1px 1px 1px 1px;
}

.purchase_cart_title_product li:first-child img{
    width:  50px;
}
.purchase_cart_title_product li:first-child p{
     margin-left:  1rem;
}
.purchase_cart_title_product li:nth-child(2){
    width: 50px;
}
.purchase_cart_title_product li:nth-child(3){
    width: 100px;
}
.purchase_cart_title_product li:nth-child(4){
    width: 50px;
}
.purchase_cart_title_product li:last-child{
    width: 75px;
    border-width: 1px 1px 1px 0;  
}

.purchase_cart_title_product_box_para{
    width: 20px;
    height: 20px;
    padding: 0.4rem;
    background-color: black;
    color: white;
    text-align: center;
}



.purchase_cart_summary{
    width: 300px;
}

.favourite_cart_section{
    padding-top: 5rem;
    padding-bottom: 5rem;
    display: flex;
    flex-direction: row;
    justify-content: center;
    background-color: white;
}
.favourite_cart_title {
}
    
.favourite_cart_title_product{
    margin: 0;
    padding: 0;
