}
.favourite_cart_title_product li{
    padding: 2rem;
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    border: 0 solid black;
    border-width: 0 1px 1px 1px;
}
.favourite_cart_title_product:first-child li {
    border-width: 1px 1px 1px 0;
}

.favourite_cart_title_product li:first-child{
    width: 300px;
    justify-content: flex-start;
    border-width: 1px 1px 1px 1px;
}

.favourite_cart_title_product li:first-child img{
    width:  50px;
}
.favourite_cart_title_product li:first-child p{
     margin-left:  1rem;
}
.favourite_cart_title_product li:nth-child(2){
    width: 50px;
}
.favourite_cart_title_product li:nth-child(3){
    width: 100px;
}
.favourite_cart_title_product li:last-child{
    width: 100px;
    border-width: 1px 1px 1px 0;  
}

.favourite_cart_title_product_box_para{
    width: 20px;
    height: 20px;
    padding: 0.4rem;
    background-color: black;
    color: white;
    text-align: center;
}



.favourite_cart_summary{
    width: 300px;
}
.product_details_container{
    margin: 1rem;
    padding: 1rem;
    display: flex;
    background-color:blanchedalmond
}

.product_details_display{
    width: 500px;
    margin-right: 10rem;
}
